.technologies {
  background-color: rgba(0, 0, 0, 0.88);
  padding: 80px 0;
  // width: 100%;
  
  .tech_wrapper {
    text-align: center;

    .tech {
      display: flex;
      justify-content: space-between;
      padding-top: 40px;


      .tech_block {
        background-color: rgba(255, 255, 255, 0.01);
        height: 180px;
        width: 200px;
        padding: 40px 0;
        border-radius: 25px;
        cursor: pointer;
        position: relative;
        // transform-style: preserve-3d;

        ul {
          display: none;
        }

        img {
          position: absolute;
          top: 40%;
          left: 50%;
          transform: translate(-50%, -50%);
        }

        h3 {
          position: absolute;
          top: 80%;
          left: 50%;
          transform: translate(-50%, -50%);
          padding-top: 5px;
        }

        transition: transform 0.5s ease;

        &:hover {
          animation: tech 1.5s 1 forwards;
          img,
          h3 {
            display: none;
          }

          ul {
            display: block;

            li {
              padding-bottom: 3px;
            }
          }
        }

        @keyframes tech {
          0% {
            transform: rotateY(0deg);
            background-color: rgba(51, 51, 51, 1);
          }
          100% {
            transform: rotateY(360deg);
            background-color: rgba(255, 255, 255, 0.06);
          }
        }
      }
    }
    .tech_title {
      text-align: center;
      position: relative;
      padding-bottom: 40px;

      h1 {
        font-size: 54px;
        letter-spacing: 2px;
        font-weight: 800;
      }

      span {
        position: absolute;
        top: 64px;
        height: 2px;
        transform: translateX(-50%);
        width: 15%;
        background-color: #56b26f;
        transition: width 1s ease;
      }

      &:hover {
        span {
          width: 30%;
          transition: width 1s ease;
        }
      }
      padding-bottom: 40px;
    }
  }
}

@media (max-width: 1200px) {
  .technologies {
    .tech_wrapper {
      .tech_title {
        padding-bottom: 0;
        h1 {
          font-size: 42px;
        }

        span {
          display: none;
        }
      }
      .tech {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;

        .tech_block {
          margin-top: 40px;

          h3 {
            font-size: 24px;
          }
        }
      }
    }
  }
}