.about {
  background-color: rgba(0, 0, 0, 0.88);
  padding-top: 80px;
  padding-bottom: 80px;
  width: 100%;

  .about_wrapper {
    h1 {
      font-size: 74px;
      padding-bottom: 40px;
    }

    .about_block1,
    .about_block2 {
      padding-top: 40px;
      display: flex;
      justify-content: space-between;

      .about_text {
        .about_title {
          h1 {
            font-size: 34px;
          }
        }
      }
    }

    img {
      border-radius: 15px;
    }

    .about_main1 {
      text-align: left;
      padding-top: 20px;
      margin: 0 180px 0 0;
    }

    .about_main2,
    .about_title2 {
      margin-left: 40px;

      h1 {
        font-size: 34px;
        padding-bottom: 20px;
      }
    }
  }
}

@media (max-width: 1000px) {
  .about {
    .about_wrapper {
      img {
        display: none;
      }

      h1 {
        font-size: 74px;
        padding-bottom: 11px;
        text-align: center;
      }

      .about_main1,
      .about_main2 {
        padding-top: 0;
        margin: 0 0;
        text-align: center;
      }
    }
  }
}
